.widgetFixed {
  position: fixed;
  bottom: 0;
  width: 100%;
  right: 0;
  z-index: 100;
  @media screen and (min-width: 500px) {
    position: fixed;
    z-index: 100;
    bottom: 5%;
    right: 5%;
    width: 250px;
    background: linear-gradient(180deg, #ffe600 0%, #ffd600 100%);
    border-radius: 5px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
  }
  &Inner {
    padding: 15px;
    background: #fff;
    border-radius: 5px;
    @media screen and (min-width: 500px) {
      box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
    }
  }
  &Box {
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
  }
  &Button {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000;
    border-radius: 30px;
    font-weight: 700;
    font-size: 14px !important;
    transition: 0.5s;
    margin: 5px auto !important;
    border: 1px solid #ffe600;
    background-color: #fff;
    padding: 10px 30px;
    @media screen and (min-width: 500px) {
      padding: 10px 0;
      width: 100%;
    }
    &:hover {
      box-shadow: 0 2px 6px 1px rgba(0, 0, 0, 0.1);
    }
    &Styled {
      background: linear-gradient(180deg, #ffe600 0%, #ffd600 100%);
      box-shadow: 0 4px 0 #e1b100;
      &:hover {
        box-shadow: 0 2px 6px 1px rgba(0, 0, 0, 0.1);
      }
    }
    &ChatStyle {
      border: none;
      background-color: transparent;
      color: #000 !important;
      &:hover {
        box-shadow: none;
      }
    }
    &Secondary {
      background: #fff;
      border-bottom: 1px dashed #141414;
      border-radius: inherit;
      padding: 5px 0;
      border: none;
      border-bottom: 1px dashed #141414;
    }
  }
  &OffSeason {
    display: grid;
    gap: 17px;
    font-size: 12px;
    line-height: 20px;
    padding: 20px 40px;
    color: #000;
    background: linear-gradient(180deg, #ffe600 0%, #ffd600 100%);
    border-radius: 5px 5px 0 0;
    @media screen and (min-width: 500px) {
      padding: 20px;
    }
  }
  ol {
    padding-left: 15px;
    margin-bottom: 0;
    font-size: 12px;
    li {
      list-style: decimal;
    }
  }
  &Title {
    font-weight: 700;
    font-size: 14px;
  }
}
