.popup {
  &Top {
    grid-area: popupTop;
    @media screen and (max-width: 500px) {
      padding-top: 1.5rem;
    }
    &Transparent {
      background: transparent;
      z-index: 10;
      height: 0px;
    }
  }
  &Action {
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 2.3rem;
  }
  &Title {
    font-weight: 700;
    font-size: 1.4rem;
    line-height: 1.65rem;
    color: #141414;
    text-align: center;
    margin: 1rem auto;
  }
  &Back,
  &Close {
    width: 3.5rem;
    height: 3.5rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: transparent;
    border: none;
  }
  &StepList {
    display: grid;
    grid-auto-flow: column;
    gap: 2rem;
  }
  &StepItem {
    height: 1.1rem;
    width: 1.1rem;
    border: 1px solid #b1b1b1;
    box-sizing: border-box;
    border-radius: 50%;
    &Active {
      background-color: #b1b1b1;
    }
  }
  &Close {
    justify-content: flex-end;
  }
}
