.certificate--order {
  ol {
    counter-reset: myCounter;
    font-size: 1.4rem;
    padding-left: 0;
    margin: 0;
    li {
      list-style: none;
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      margin: 1.6rem 0;
      &:first-child {
        margin-top: 0;
      }
      &:before {
        counter-increment: myCounter;
        content: counter(myCounter);
        display: inline-block;
        text-align: center;
        margin: 0 1.3rem 0 0;
        font-weight: 700;
      }
      &.li_none::before {
        opacity: 0;
      }
    }
  }
}
