.widgetModal {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  overflow-y: auto;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  &Inner {
    position: fixed;
    width: 100%;
    max-width: 100%;
    z-index: 1001;
    background-color: #fff;
    border-radius: 3px;
    @media screen and (min-width: 500px) {
      box-shadow: 0 0 6px 2px rgba(50, 50, 50, 0.3);
      bottom: 5%;
      right: 5%;
      width: 340px;
    }
    .widgetModalFrame {
      min-width: 100%;
      max-width: 100%;
      height: 100vh;
      min-height: calc(var(--vh, 1vh) * 100);
      height: calc(var(--vh, 1vh) * 100);
      @media screen and (min-width: 500px) {
        height: 90vh;
        min-height: calc(var(--vh, 1vh) * 90);
        height: calc(var(--vh, 1vh) * 90);
      }
    }
  }
}

.popupInner {
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas:
    "popupTop"
    "popupContent"
    "popupBottom";
  @media screen and (min-width: 500px) {
    height: 90vh;
  }
}

.popupContent {
  grid-area: popupContent;
  overflow: overlay;
}
