.program {
  &Item {
    background: #f4f4f4;
    margin-bottom: 20px;
    overflow: hidden;
    &Top {
      position: sticky;
      top: 0;
      background-color: #f4f4f4;
      display: grid;
      gap: 10px;
      z-index: 10;
      padding: 20px 0;
    }
    &Title {
      font-weight: 700;
      font-size: 14px;
      line-height: 16.5px;
    }
  }
  &DetailBtns {
    display: flex;
    justify-content: space-between;
  }
}

.detailText {
  display: grid;
  gap: 20px 0;
  margin-bottom: 20px;
  line-height: 16px;
}

.container {
  padding: 0 20px 20px;
}
.pictures {
  margin-bottom: 20px;
  div {
    position: relative;
    div {
      width: 350px;
      div {
        display: flex;
        div {
          margin: 0 10px;
          height: 300px;
          width: 300px;
          div {
            margin: 0;
          }
        }
        img {
          object-fit: cover;
        }
        video {
          min-height: 50px;
          max-width: 100%;
          max-height: 350px;
          margin-left: auto;
          margin-right: auto;
          display: block;
          &:active {
            background-color: #000;
          }
        }
      }
    }
  }
}

.slickNext,
.slickPrev {
  font-size: 0 !important;
  line-height: 0 !important;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: 0;
  background: 0 0;
  background-color: rgba(#fff, 0.6);
  border-radius: 50%;
  -webkit-appearance: button;
  overflow: visible;
  transition: background-color 0.3s ease-in-out, width 0.5s ease-in-out,
    height 0.5s ease-in-out;
  &:hover {
    background-color: rgba(#fff, 0.8);
    width: 25px;
    height: 25px;
    &::before {
      font-size: 30px;
      line-height: 10px;
    }
  }
  &::before {
    position: relative;
    font-family: monospace;
    font-size: 22px;
    line-height: 10px;
    top: 0;
    opacity: 0.75;
    color: #000;
    -webkit-font-smoothing: antialiased;
    transition: font-size 0.4s ease-in-out, line-height 0.3s ease-in-out;
  }
}
.slickPrev {
  left: 10px;
  z-index: 2;

  &::before {
    content: "←";
  }
}
.slickNext {
  right: 10px;
  z-index: 2;
  &::before {
    content: "→";
  }
}
